.testimonials {
  display: grid;
  grid-gap: 65px;
  grid-template-columns: repeat(3, 200px);
  justify-content: center;
}

.person {
  text-align: center;
  font-size: 16px;
  line-height: 24px;
  
  &__photo {
    width: 150px;
    height: 150px;
    border-radius: 50%;
  }

  &__name {
    margin-top: 12px;
  }

  &__position {
    color: #949494;
    font-style: italic;
    margin-top: -15px;
  }
  &__about {
    position: relative;
    &::after {
      display: block;
      content: '';
      position: absolute;
      width: 40px;
      height: 33px;
      background-image: url('../images/quotes.svg');
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
      right: calc(100% + 6px);
      bottom: calc(100% + 6px);
    }
  }
}

@media screen and (max-width: $small-screen) {
  .testimonials {
    grid-template-columns: repeat(1, 200px);
  }
}