.section {
  padding: 100px calc(50% - 470px);
  background-color: white;
  display: flex;
  flex-direction: column;
  
  &:nth-child(even) {
    background-color: #EEEEEE;
  }

  &__heading {
    font-size: 48px;
    text-transform: uppercase;
    font-weight: 300;
    line-height: 48px;
    display: inline-block;
    text-align: center;
    
    position: relative;
    width: auto;
    margin: -130px auto 100px auto;

    &:before {
      display: block;
      content: '';
      position: absolute;
      width: 100%;
      left: 0;
      top: calc(100% + 5px);
      height: 20px;
      background: url('../images/crown-line.svg') center center no-repeat;
      background-size: 100% 100%;
    }
  }
}

@media screen and (max-width: $small-screen) {
  .section {
    flex-direction: column;
    padding-top: 100px;
  }
}