.header {
  height: 100vh;
  background-image: url('../images/header.jpg');
  background-size: cover;
  background-position: center;
  position: relative;
  font-weight: 400;


  &__nav {
    position: absolute;
    bottom: 25px;
    left: 0;
    right: 0;
  }

  &__top {
    position: absolute;
    top: 35px;
    left: calc(50% - #{$logo-width / 2});
    display: flex;
    color: white;
  }

  &__phone {
    margin: 0 82px;
  }

  &__phone, &__action {
    position: relative;
    top: 47px;
  }

  &__link {
    color: white;
    text-decoration: none;
    border-bottom: 1px dashed;
    transition: color $hover-duration;
    text-transform: uppercase;
  }

  &:hover {
    color: $active-color;
  }
}


@media screen and (max-width: $small-screen) {
  .header {
    &__top {
      flex-direction: column;
      align-items: center;
    }

    &__phone {
      margin: -30px 0 10px 0;
    }
  }
}