.form {
  display: flex;
  flex-direction: column;
  
  
  &__item {
   margin-bottom: 8px; 
  }

  &__input, &__button {
    width: 100%;
   
  }

  &::placeholder {
    color: inherit;
  }

  &__input {
    height: 54px;
    background-color: #EEEEEE;
    text-align: center;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 14px;
    line-height: 36px;
  }

  &__textarea {
    height: 156px;
    
  }
  
  &__button {
    height: 54px;
    background-color: #00AAEF;
    font-weight: 500;
    font-size: 14px;
    line-height: 36px;
    color: white;
    text-transform: uppercase;
  }
}
