.info {
  display: flex;
  justify-content: space-between;

  &__heading {
    width: 314px;
    font-weight: 300;
    text-transform: uppercase;
    line-height: 60px;
    font-size: 48px;
    margin-top: 0;
  }

  &__text {
    max-width: 564px;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    
    > :first-child {
      margin-top: 0;
    }
    > :last-child {
      margin-bottom: 0;
    }
  }

  a, a:visited {
    color: #00AAEF;
    text-decoration: none;
  }
}


@media screen and (max-width: $small-screen) {
  .info {
    flex-direction: column;
    align-items: center;
  
    &__heading {
      max-width: 600px;
      width: 100%;
    }
  }
}