
.photos {
  display: flex;
  justify-content: space-between;

  &__img {
    height: 260px;
    width: calc(50% - 10px);
    object-fit: cover;
    object-position: center;
  }

  &_whide_left {
    .photos__img {
      &:first-child {
        width: calc(67% - 10px)
      }
      &:last-of-type {
        width: calc(33% - 10px);
      }
    }
  }
  &_whide_right {
    .photos__img {
      &:first-child {
        width: calc(33% - 10px)
      }
      &:last-of-type {
        width: calc(67% - 10px);
      }
    }
  }
}

.gallary__cap {
  margin: 12px auto 35px 0;
  font-size: 16px;
  a {
    text-decoration: none;
    color: #00AAEF;
  }
}

.gallary__btn {
  display: block;
  margin: 0 auto;
  width: 188px;
  height: 52px;
  font-size: 14px;
  color: white;
  background-color: #00AAEF;
  text-transform: uppercase;
  border: none;
}
