.consultation {
  display: flex;
  justify-content: space-between;

  &__text {
    width: 480px;
    font-size: 16px;
    line-height: 28px;
  }

  &__heading {
    font-size: 32px;
    line-height: 40px;
    margin: 0 24px 0 0;
  }

  &__form {
    width: 380px;
  }
}


@media screen and (max-width: $small-screen) {
  .consultation {
    flex-direction: column;
    align-items: center;
    &__heading {
      text-align: center;
    }
    &__form {
      margin-top: 30px;
    }
  }
}
